<template>
  <div class="relative">
    <div class="vx-navbar-wrapper p-0">
      <vs-navbar class="navbar-custom navbar-skelton" :class="navbarClasses" :style="navbarStyle" :color="navbarColor">

<!--                <bookmarks :navbarColor="navbarColor" v-if="windowWidth >= 992"/>-->

        <router-link tag="div" to="/" class="vx-logo cursor-pointer mx-auto flex items-center">
         <!-- <logo class="w-10 mr-4 fill-current text-primary"/> -->
          <span class="vx-logo-text text-primary">{{this.$store.state.institutionName}}</span>
        </router-link>

        <!--        <search-bar/>-->

        <!--        <notification-drop-down/>-->

        <!--        <profile-drop-down/>-->

        <vs-navbar-item index="0">
          <router-link to="/">Applicant Register</router-link>
        </vs-navbar-item>
        <vs-navbar-item index="0">
          <vs-input class="inputx mr-2" placeholder="Applicant ID" v-model="applicantId" />
        </vs-navbar-item>
        <vs-navbar-item index="0">
          <vs-button color="primary" type="border" icon-pack="feather" @click="searchItem" icon="icon-search"></vs-button>
        </vs-navbar-item>

      </vs-navbar>
    </div>
  </div>
</template>

<script>
  import Bookmarks from "./components/Bookmarks.vue"
  import SearchBar from "./components/SearchBar.vue"
  import NotificationDropDown from "./components/NotificationDropDown.vue"
  // import ProfileDropDown from "./components/ProfileDropDown.vue"
  import Logo from "../Logo"

  export default {
    name: "the-navbar-horizontal",
    props: {
      logo: {type: String},
      navbarType: {
        type: String,
        required: true
      }
    },
    mounted(){
      this.applicantId = null

    },
    data(){
      return{
        applicantId: null,
      }
    },
    components: {
      Logo,
      Bookmarks,
      SearchBar,
      NotificationDropDown,
      // ProfileDropDown,
    },
   methods:{
    searchItem(){
      this.$root.$emit('searchApplicant', this.applicantId)
    }
   },
    computed: {
      navbarColor() {
        let color = "#fff"
        if (this.navbarType === "sticky") color = "#f7f7f7"
        else if (this.navbarType === 'static') {
          if (this.scrollY < 50) {
            color = "#f7f7f7"
          }
        }

        this.isThemedark === "dark" ? color === "#fff" ? color = "#10163a" : color = "#262c49" : null

        return color
      },
      isThemedark() {
        return this.$store.state.theme
      },
      navbarStyle() {
        return this.navbarType === "static" ? {transition: "all .25s ease-in-out"} : {}
      },
      navbarClasses() {
        return this.scrollY > 5 && this.navbarType === "static" ? null : "d-theme-dark-light-bg shadow-none"
      },
      scrollY() {
        return this.$store.state.scrollY
      },
      verticalNavMenuWidth() {
        return this.$store.state.verticalNavMenuWidth
      },
      windowWidth() {
        return this.$store.state.windowWidth
      },
    }
  }
</script>

